import Link from 'next/link';
import Image from 'next/image';
import { Card, CardContent, CardFooter, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import UserAvatar from '@/components/user-avatar';
import type { Event, EventStatus } from '@/lib/types';
import { CalendarDays, Users, KeyRound, ArrowRight, MapPin } from 'lucide-react';
import { format, parseISO } from 'date-fns';
import { getEventStatus } from '@/lib/data'; // Ensure this handles dates correctly

interface EventCardProps {
  event: Event;
}

export default function EventCard({ event }: EventCardProps) {
  const eventStatus = getEventStatus(event.date);
  const formattedDate = format(parseISO(event.date), 'MMM d, yyyy HH:mm');

  const statusColors: Record<EventStatus, string> = {
    upcoming: 'bg-blue-500 hover:bg-blue-600',
    current: 'bg-green-500 hover:bg-green-600',
    past: 'bg-gray-500 hover:bg-gray-500',
  };

  return (
    <Card className="flex flex-col overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300 rounded-lg">
      {event.imageUrl && (
        <div className="relative w-full h-48">
          <Image
            src={event.imageUrl}
            alt={event.name}
            layout="fill"
            objectFit="cover"
            data-ai-hint="event banner"
          />
           <Badge
            className={`absolute top-2 right-2 text-white px-2 py-1 text-xs font-semibold rounded ${statusColors[eventStatus]}`}
          >
            {eventStatus.toUpperCase()}
          </Badge>
        </div>
      )}
      <CardHeader>
        <CardTitle className="text-xl font-bold">{event.name}</CardTitle>
        <CardDescription className="flex items-center text-sm text-muted-foreground pt-1">
          <CalendarDays className="mr-2 h-4 w-4" /> {formattedDate}
        </CardDescription>
      </CardHeader>
      <CardContent className="flex-grow">
        <p className="text-sm text-muted-foreground line-clamp-3 mb-4">
          {event.description.split('\n')[0]} {/* Show first line or summarized description */}
        </p>
        <div className="flex items-center space-x-2 mb-2">
          <Users className="h-4 w-4 text-muted-foreground" />
          <span className="text-sm">
            {event.participants.length} / {event.participantLimit || 'âˆž'} participants
          </span>
        </div>
        <div className="flex items-center space-x-2 text-sm text-muted-foreground">
          <KeyRound className="h-4 w-4" />
          <span>CD-Key: <Badge variant="secondary">{event.cdKey}</Badge></span>
        </div>
        {event.participants.length > 0 && (
          <div className="mt-4">
            <h4 className="text-xs font-semibold mb-1 text-muted-foreground">Participants:</h4>
            <div className="flex -space-x-2 overflow-hidden">
              {event.participants.slice(0, 5).map((p) => (
                <UserAvatar key={p.id} user={p} className="h-6 w-6 border-2 border-card" />
              ))}
              {event.participants.length > 5 && (
                <div className="flex items-center justify-center h-6 w-6 rounded-full bg-muted text-muted-foreground text-xs border-2 border-card">
                  +{event.participants.length - 5}
                </div>
              )}
            </div>
          </div>
        )}
      </CardContent>
      <CardFooter>
        <Button asChild className="w-full" variant="default">
          <Link href={`/events/${event.id}`}>
            View Event <ArrowRight className="ml-2 h-4 w-4" />
          </Link>
        </Button>
      </CardFooter>
    </Card>
  );
}
