export async function getMockUserById(userId: string): Promise<User | undefined> {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 200));
  const user = mockUsers.find(u => u.id === userId);
  return user ? JSON.parse(JSON.stringify(user)) : undefined;
}

// Mock function for Blessing Skin Server OAuth - returns a mock user
export async function mockOAuthLogin(username?: string, password?: string): Promise<User | null> {
  await new Promise(resolve => setTimeout(resolve, 500));
  // In a real scenario, you'd verify credentials with Blessing Skin Server
  // For mock, just return 'Steve' if no specific user is requested, or find by username
  if (username) {
    const user = mockUsers.find(u => u.username.toLowerCase() === username.toLowerCase());
    return user || null;
  }
  return mockUsers[0]; // Default to Steve
}

export async function getUserProfile(userId: string): Promise<{ user: User; joinedEvents: Event[] } | null> {
  await new Promise(resolve => setTimeout(resolve, 400));
  const user = mockUsers.find(u => u.id === userId);
  if (!user) return null;

  const joinedEvents = mockEvents.filter(event => user.joinedEventIds?.includes(event.id));
  return { user: JSON.parse(JSON.stringify(user)), joinedEvents: JSON.parse(JSON.stringify(joinedEvents)) };
}

