import EventCard from '@/components/event-card';
import { getEvents, getEventStatus } from '@/lib/data';
import type { Event } from '@/lib/types';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { ListChecks } from 'lucide-react';

export const dynamic = 'force-dynamic'; // Ensure data is fetched on each request for dynamic content

export default async function HomePage() {
  const events = await getEvents();

  const upcomingEvents = events.filter(event => getEventStatus(event.date) === 'upcoming');
  const currentEvents = events.filter(event => getEventStatus(event.date) === 'current');
  const pastEvents = events.filter(event => getEventStatus(event.date) === 'past');

  const renderEventGrid = (eventList: Event[], title: string) => {
    if (eventList.length === 0) {
      return (
        <Alert className="mt-4">
          <ListChecks className="h-4 w-4" />
          <AlertTitle>No Events</AlertTitle>
          <AlertDescription>
            There are currently no {title.toLowerCase()} events. Check back later!
          </AlertDescription>
        </Alert>
      );
    }
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {eventList.map(event => (
          <EventCard key={event.id} event={event} />
        ))}
      </div>
    );
  };

  return (
    <div className="space-y-8">
      <section className="text-center py-8 bg-gradient-to-r from-primary/10 via-background to-accent/10 rounded-lg shadow-sm">
        <h1 className="text-4xl font-bold tracking-tight text-foreground sm:text-5xl">
          Welcome to <span className="text-primary">MineVent</span>
        </h1>
        <p className="mt-4 text-lg leading-8 text-muted-foreground">
          Discover, join, and create exciting Minecraft events.
        </p>
      </section>

      <Tabs defaultValue="upcoming" className="w-full">
        <TabsList className="grid w-full grid-cols-3 mb-6">
          <TabsTrigger value="upcoming">Upcoming ({upcomingEvents.length})</TabsTrigger>
          <TabsTrigger value="current">Current ({currentEvents.length})</TabsTrigger>
          <TabsTrigger value="past">Past ({pastEvents.length})</TabsTrigger>
        </TabsList>
        <TabsContent value="upcoming">
          <h2 className="text-2xl font-semibold mb-4 text-foreground">Upcoming Events</h2>
          {renderEventGrid(upcomingEvents, "Upcoming")}
        </TabsContent>
        <TabsContent value="current">
          <h2 className="text-2xl font-semibold mb-4 text-foreground">Current Events</h2>
          {renderEventGrid(currentEvents, "Current")}
        </TabsContent>
        <TabsContent value="past">
          <h2 className="text-2xl font-semibold mb-4 text-foreground">Past Events</h2>
          {renderEventGrid(pastEvents, "Past")}
        </TabsContent>
      </Tabs>
    </div>
  );
}
